<template>
    <div v-loading="loading">
        <top-nav mdl="sucai" :select="1"></top-nav>

        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form label-width="80px" size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="分类名称">
                        <el-input v-model="searchForm.title"></el-input>
                    </el-form-item>
                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getCatList(1)" size="mini" type="primary">搜索</el-button>
                        <a href="javascript:void(0);" @click="searchForm.title = '',getCatList()">取消搜索</a>
                        <el-button size="mini"  type="danger" @click="createCat(false)">新增分类</el-button>
                    </div>
                </el-form>

                <el-table :data="datas.list" style="width: 100%" size="mini">
                    <el-table-column prop="category_id" label="分类ID"></el-table-column>
                    <el-table-column prop="title" label="分类名称"></el-table-column>
                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <el-button v-if="type == 'select'"  @click="selectData(scope.row)" size="mini" type="text">确定选择</el-button>
                            <el-button v-if="type!='select'" @click="editCat(scope.row)" size="mini" type="text">编辑</el-button>
                            <el-popover v-if="type!='select'" style="margin: 0 10px;" placement="top" width="160"
                                         v-model="scope.row.is_delete">
                                <p style="margin-bottom: 4px">确定删除吗？</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button  :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消
                                    </el-button>
                                    <el-button :loading="loading" type="primary" size="mini" @click="deleteCat(scope.row)">确定
                                    </el-button>
                                </div>
                                <el-button type="text" slot="reference">删除</el-button>
                            </el-popover>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>


        <!--编辑新增 弹窗-->
        <el-dialog title="分类"  v-if="type!='select'" v-loading="subLoading" :visible.sync="dialog" width="30%" :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small">
                <el-form-item label="分类名称" label-width="80px">
                    <el-input v-model="form.title" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false">取 消</el-button>
                <el-button size="small" type="primary" @click="createCat(true)">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'productCategoryIndex',
        components: {TopNav},
        props:['type'],
        data() {
            return {
                //修改 添加 弹窗  菊花
                subLoading: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                //修改 添加 状态控制
                dialog: false,
                //修改 添加 form
                form: {
                    title: '',  // 标题
                },
                //搜索 form
                searchForm: {
                    title: '',  // 标题
                },
                categoryId: 0,
            }

        },
        created() {
            this.getCatList();
        },
        methods: {
            selectData(row){
                this.$emit('select',{id:row.category_id,title:row.title});
            },
            /**
             * 获取分类列表
             * @param page {number} 页数
             */
            getCatList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.resource.category/index", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    title: this.searchForm.title,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });

            },

            /**
             * 添加分类
             * @param sub {boolean} 是否是 提交操作
             */
            createCat(sub = true) {
                if (sub == false) {
                    this.dialog = true;
                    this.form.title = "";
                    this.categoryId = 0;
                } else {
                    this.subLoading = true;
                    var url = "/shop/admin.resource.category/create";
                    if (this.categoryId > 0) {
                        url = "/shop/admin.resource.category/edit";
                    }
                    api.post(url, {
                        appToken: this.$store.getters.appToken,
                        title: this.form.title,
                        category_id: this.categoryId,
                    }).then(res => {
                        this.form.role_name = "";
                        this.subLoading = false;
                        this.dialog = false;
                        this.$message({
                            message: '恭喜你，操作成功',
                            type: 'success',
                        });
                        this.getCatList();
                    }).catch((error) => {
                        this.subLoading = false;
                        this.dialog = false;
                    });
                }
            },

            /**
             * 删除分类
             * @param row {data} 操作的记录数据对象
             */

            deleteCat(row) {
                this.loading = true;
                api.post("/shop/admin.resource.category/del", {
                    appToken: this.$store.getters.appToken,
                    category_id:row.category_id
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.getCatList();
                }).catch((error) => {
                    this.loading = false;
                });
            },


            /**
             * 修改分类弹出
             * @param row {data} 操作的记录数据对象
             */
            editCat(row) {
                this.dialog = true;
                this.form.title = row.title;
                this.categoryId = row.category_id;
                this.form.category_id = row.category_id;
            },
        }
    }
</script>
